.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.search-form {
  justify-content: center;
  margin-bottom: 24px;
  padding: 8px;
  display: flex;
}

.button {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.button is-hidden, .button > .spinner.is-hidden {
  display: none;
}

.articles {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  list-style: none;
  display: grid;
}

.articles li {
  height: 450px;
  overflow: hidden;
}

.articles img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.articles a {
  color: #2a2a2a;
  text-decoration: none;
}

